  outline: none;
}

input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 7.3px;
  cursor: pointer;
  box-shadow: 0px 0px 0px rgba(0, 0, 0, 0), 0px 0px 0px rgba(13, 13, 13, 0);
  background: #008390;
  border-radius: 25px;
  border: 1.2px solid rgba(1, 1, 0, 0.32);
