  background: #008390;
}

input[type=range]::-moz-range-track {
  width: 100%;
  height: 7.3px;
  cursor: pointer;
  box-shadow: 0px 0px 0px rgba(0, 0, 0, 0), 0px 0px 0px rgba(13, 13, 13, 0);
  background: #008390;
  border-radius: 25px;
  border: 1.2px solid rgba(1, 1, 0, 0.32);
}

input[type=range]::-moz-range-thumb {
  box-shadow: 0.9px 0.9px 0px rgba(0, 0, 0, 0), 0px 0px 0.9px rgba(13, 13, 13, 0);
  border: 0.5px solid rgba(0, 0, 0, 0.31);
  height: 23px;
  width: 23px;
  border-radius: 50px;
  background: #1C154B;
  cursor: pointer;
}

input[type=range]::-ms-track {
  width: 100%;
  height: 7.3px;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  color: transparent;
}

input[type=range]::-ms-fill-lower {
  background: rgba(0, 0, 104, 0);
  border: 1.2px solid rgba(1, 1, 0, 0.32);
